import React from "react";
import "./index.scss";
import "tachyons";
import Logo from "./Logo";
import WhatshotIcon from "@material-ui/icons/Whatshot";
import MenuBookIcon from "@material-ui/icons/MenuBook";
import AssessmentIcon from "@material-ui/icons/Assessment";
import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import HelpIcon from "@material-ui/icons/Help";
import Header from "./Header";
import SearchComp from "./SearchComp";
import Card from "./Card";
import HorizontalLabelPositionBelowStepper from "./Stepper";
function App() {
  return (
    <div className="parent">
      <div className="child1">
        <div className="logo">
          <Logo />
        </div>

        <div className="orders">
          <div className="whatshotIcon">
            <WhatshotIcon />
            <div className="order-text">ORDERS</div>
          </div>
        </div>

        <div className="menu">
          <div className="MenuBookIcon">
            <MenuBookIcon />
            <div className="menu-text"> MENU</div>
          </div>
        </div>

        <div className="metrices">
          <div className="AssessmentIcon">
            <AssessmentIcon />
            <div className="metrices-text">MATRICES</div>
          </div>
        </div>

        <div className="discounts">
          <div className="AttachMoneyIcon">
            <AttachMoneyIcon />
            <div className="discount-text">DISCOUNT</div>
          </div>
        </div>

        <div className="profile">
          <div className="AccountCircleIcon">
            <AccountCircleIcon />
            <div className="profile-text">PROFILE</div>
          </div>
        </div>

        <div className="help">
          <div className="HelpIcon">
            <HelpIcon />
            <div className="help-text">HELP</div>
          </div>
        </div>
      </div>

      <div className="child2">
        <div className="mynav">
          <Header />
        </div>

        <div className="search">
          <div className="status">
            <div className="searchComp">
              <SearchComp />
            </div>
            <div className="cardComp">
              <Card />
              <Card />
            </div>
          </div>
        </div>

        <div className="checkout">
          <div className="main">
            <HorizontalLabelPositionBelowStepper />
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
